import request from '@/common/js/request'
import { getUrl } from '@/common/js/catch'
import axios from '../index'
// 获取FileList
export function getFileList(filegroup) {
  return request({
    url: getUrl('VUE_APP_FILE_API') + '/app/file/upload/list/' + filegroup,
    method: 'post'
  })
}
