<template>
  <div :id="containerId" class="upload-plus" :class="uploadClass">

    <!-- <div v-show="!readonly" style="display:inline;"> -->
    <!-- text 样式 自动上传-->
    <van-button
      v-if="listType == 'text' && autoUpload && isShow"
      :id="selectFileId"
      :size="btnSize"
      icon="upgrade"
      type="info"
      plain
      @click="startUpload()"
    >
      点击上传
    </van-button>
    <div
      v-show="showPictureSelectBtn"
      :id="selectFileId"
      class="el-upload el-upload--picture-card mb8"
    >
      <slot name="picture-wrap">
        <i class="el-icon-plus" />
      </slot>
    </div>
    <!-- </div> -->
    <slot name="tip" />
    <!--如果显示文件列表 且 是 text 方式显示-->
    <div class="uploadregion">
      <ul v-if="showFileList && listType === 'text'" class="filelist">
        <li
          v-for="(item, i) in fileList"
          :key="item.id ? item.id : item.localId"
          tabindex="0"
          :class="item.waitUpload ? 'is-ready' : 'is-success'"
          :style="readonly&&i===0?'margin-top:0px':''"
        >
          <slot name="textLabelSlot" :data="{item,index:i}" />
          <div class="fj-icon"><i class="iconfont iconiconset0118" /></div>
          <div class="fname" @click="clickFileHandler(item)">{{ item.name }}</div>
          <!-- <label v-if="ckzt" class="el-upload-list__item-status-label" :class="{'block-im': readonly}">
            <i class="iconfont iconxuanzhong" />
          </label> -->
          <!-- <el-tooltip v-if="!readonly && isShow" content="点击删除文件"> -->
          <div v-if="isShow" class="btn-del">
            <!-- 上传成功图标 -->
            <!-- <i class="iconfont iconxuanzhong" /> -->
            <i class="iconfont iconshanchu" @click="removeFile(item)" />
          </div>

        <!-- </el-tooltip> -->
        <!-- <el-progress v-if="item.waitUpload" :stroke-width="strokeWidth" :percentage="item.percent" /> -->
        </li>
      </ul>
    </div>
    <!-- <el-dialog
      top="7vh"
      :visible.sync="dialogVisible"
      custom-class="upload-image-dialog"
      size="tiny"
      style="z-index: 100000"
      :append-to-body="true"
    >
      <img width="100%" :src="dialogImagePath" alt="">
    </el-dialog> -->

  </div>
</template>
<script>
import plupload from 'plupload'
import { getFileList, deletedTempFile } from '@/api/gg/files'
import { getUrl } from '@/common/js/catch'
function noop() {
}

export default {
  name: 'UploadPlus',
  props: {
    ckzt: {
      type: Boolean,
      default: true
    },
    isShow: {
      type: Boolean,
      default: true
    },
    filegroup: {
      type: String,
      default: ''
    },
    uploadClass: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: 'file'
    },
    filed: {
      type: String
    },
    action: {
      type: String,
      default: getUrl('VUE_APP_FILE_API') + '/ananymous/file/upload'
    },
    drag: Boolean,
    showFileList: {
      type: Boolean,
      default: true
    },
    accept: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'select'
    },
    onSuccess: {
      type: Function,
      default: noop
    },
    beforeRemove: {
      type: Function,
      default: noop
    },
    afterRemove: {
      type: Function,
      default: noop
    },
    onError: {
      type: Function,
      default: noop
    },
    beforeUpload: {
      type: Function,
      default: noop
    },
    finishUpload: {
      type: Function,
      default: noop
    },
    autoUpload: {
      type: Boolean,
      default: true
    },
    listType: {
      type: String,
      default: 'text' // text,picture,picture-card
    },
    readonly: {// 只读模式
      type: Boolean,
      default: false
    },
    btnSize: {
      type: String,
      default: 'small' // medium, small, mini
    },
    disabled: Boolean,
    limit: {// 限制上传个数，-1为不限制
      type: Number,
      default: -1
    },
    filters: {
      type: Object,
      default() {
        return {
          ext:
                            'swf,txt,doc,docx,ppt,pptx,xls,xlsx,pdf,flv,mp4,wmv,mpeg,jpg,jpeg,png,mp3,zip,rar,cdr',
          max_file_size: '50mb' // 最大只能上传50mb的文件
        }
      }
    },
    removeType: {
      type: String,
      default: 'server' // 删除模式（服务器删除：server , 仅客户端页面移除 client）
    },
    multiple: {// 打开window文件选择时窗口时是否可以多选文件
      type: Boolean,
      default: true
    },
    showExtBtn: {
      type: Boolean,
      default: false
    },
    extBtnTip: {
      type: String,
      default: ''
    },
    onExtBtn: {
      type: Function,
      default: noop
    },
    onClickFile: {// 当模式为 “text”时，点击文件列表回调事件
      type: Function,
      default: noop
    }
  },

  data() {
    return {
      name1: '',
      fileList: [],
      deletedList: '',
      uploadedList: [],
      fileChangeInfo: {},
      _filegroup: '',
      strokeWidth: 4,
      dialogVisible: false,
      dialogImagePath: '',
      fileMap: {},
      containerId: 'plupload_container_' + this.guid(), // 上传容器id
      selectFileId: 'plupload_selectFile_' + this.guid(), // 选择文件按钮id
      postFileSelector: 'plupload_postFile_' + this.guid(), // 开始上传按钮id
      uploading: false // 上传状态：是否上传中
    }
  },
  computed: {
    showPictureSelectBtn: function() {
      if (this.listType === 'picture-card') {
        return true
      } else if (this.listType === 'picture') {
        return this.fileList.length <= 0
      } else { // text类型不显示
        return false
      }
    }
  },
  watch: {
    _filegroup() {
      this.fileChangeInfo.filegroup = this._filegroup
    },

    filegroup() {
      this._filegroup = this.filegroup
      this.getFileList()
      this.refresh()
    },
    fileChangeInfo: {
      handler: function(val) {
        this.fileChangeInfo.filegroup = this._filegroup
        this.$emit('getFileChangeInfo', this.filed, val)
      },
      deep: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.getFileList()
      this.pluploadInit()
      this.fileChangeInfo = {
        filegroup: this._filegroup,
        deletedList: this.deletedList,
        uploadedList: this.uploadedList
      }
    })
  },
  methods: {
    guid() {
      return this.S4() + this.S4() + this.S4() + this.S4() + this.S4() + this.S4() + this.S4() + this.S4()
    },

    S4() {
      return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
    },
    getSuffix(fileName) {
      var pos = fileName.lastIndexOf('.')
      var su = ''
      if (pos !== -1) {
        su = fileName.substring(pos + 1)
        su = su.toLowerCase()
      }
      return su
    },
    getFileList() {
      if (this.filegroup == null || this.filegroup === '' || this.filegroup.length < 20) {
        this._filegroup = this.guid()
      } else {
        this._filegroup = this.filegroup
        this.fileChangeInfo.filegroup = this.filegroup
        getFileList(this._filegroup).then(returnValue => {
          const fileList = returnValue.data
          for (let i = 0, length = fileList.length; i < length; i++) {
            fileList[i].url = getUrl('VUE_APP_FILE_API') + '/ananymous/file/download/' + fileList[i].id
          }
          this.fileList = fileList
          if (this.fileList.length === 0) {
            this.$emit('noFile')
          }
        }).catch((e) => {
          console.log(e)
        })
      }
    },
    /* 获取文件的名称部分（不包含后缀）*/
    getName(fileName) {
      var pos = fileName.lastIndexOf('.')
      var name1 = fileName
      if (pos != -1) {
        name1 = fileName.substr(0, pos)
      }
      return name1
    },
    removeFile(item) {
      const that = this
      if (!item || this.beforeRemove(item) === false) {
        return
      }
      // 未上传的文件
      this.fileList.splice(this.fileList.indexOf(item), 1)
      if (item.waitUpload === true || this.removeType === 'client') {
        var file = this.uploader.getFile(item.localId)
        this.uploader.removeFile(file)
        this.afterRemove(item, file)
      } else {
        if (item.id === null) {
          // 获取临时文件名称
          const filename = this.fileMap[item.localId].target_name
          // 调用删除临时文件接口
          deletedTempFile({ filename: filename, gg_file_filegroup: this._filegroup }).then(res => {
            // 移除临时上传集合中的数据
            for (let i = that.uploadedList.length - 1; i >= 0; i--) {
              if (that.uploadedList[i].id === item.localId) {
                that.uploadedList.splice(i, 1)
                break
              }
            }
            that.$toast('删除成功！')
          })
        } else {
          this.deletedList = this.deletedList === '' ? item.id : this.deletedList + ',' + item.id
          this.fileChangeInfo.deletedList = this.deletedList
        }
      }
    },
    pluploadInit(arg) {
      var that = this
      that.uploader = new plupload.Uploader({
        unique_names: true,
        runtimes: 'html5,flash,silverlight,html4',
        browse_button: that.selectFileId,
        multi_selection: that.multiple,
        chunk_size: '5mb',
        dragdrop: true,
        container: document.getElementById(that.containerId),
        url: that.action,
        filters: {
          max_file_size: that.filters.max_file_size,
          mime_types: that.filters.ext,
          prevent_duplicates: true,
          prevent_empty: false
        },
        init: {
          PostInit: function() {
            if (that.listType === 'picture' || that.listType === 'picture-card') {
              that.filters.ext = 'png,jpg,jpeg'
              that.autoUpload = true
            }
          },
          FilesAdded: function(up, files) {
            var failFileType = {}
            plupload.each(files, function(file) {
              var name = that.getName(file.name)
              that.name1 = name
              name = name.length > 50 ? name.substr(0, 50) : name
              var suffix = that.getSuffix(file.name)
              if (suffix === '' || that.filters.ext.indexOf(suffix) < 0) {
                up.removeFile(file)
                failFileType[suffix] = true
                return
              }
              var newFile = {
                name: name,
                suffix: suffix,
                id: null,
                localId: file.id, // 本地文件ID
                waitUpload: true,
                size: file.size,
                percent: 0
              }
              // 处理图片前端显示
              that.showImg(file, newFile)
              that.fileMap[file.id] = newFile
              if (that.limit === 1) {
                that.removeFile(that.fileList.shift())
              }
              that.fileList.push(newFile)
            })
            var msglist = ''
            for (var key in failFileType) {
              msglist += key + '、'
            }
            if (msglist != '') {
              msglist = msglist.slice(0, msglist.length - 1)
              // that.$message.error(
              //     '不可上传 ' + msglist + ' 类型的文件'
              // )
              that.$toast.fail('不可上传 ' + msglist + ' 类型的文件')

              // that.$message({
              //   showClose: true,
              //   message: '不可上传 ' + msglist + ' 类型的文件',
              //   type: 'error'
              // })

              return
            }
            if (that.autoUpload) {
              that.startUpload()
            }
            that.refresh()
          },
          BeforeUpload: function(up, file) {
            console.log(that._filegroup)
            that.uploader.setOption('multipart_params', {
              'filegroup': that._filegroup
            })
            that.uploader.start()
          },

          UploadProgress: function(up, file) {
            var f = that.fileMap[file.id]
            f.percent = file.percent
          },

          FileUploaded: function(up, file, res) {
            that.uploader.removeFile(file)
            if (res.status === 200) {
              var tf = that.fileMap[file.id]
              that.fileMap[file.id].target_name = file.target_name
              tf.waitUpload = false
              var resp = JSON.parse(res.response)
              if (resp.result === 'error') {
                // that.$message.error("上传文件失败")
                that.$toast.fail('上传文件失败')

                // that.$message({
                //   showClose: true,
                //   message: '上传文件失败',
                //   type: 'error'
                // })
              }

              if (resp.result === 'success') {
                if ((resp.chunks === resp.chunk + 1) || (resp.chunk === resp.chunks)) {
                  that.fileMap[file.id].target_name = file.target_name
                  that.fileMap[file.id].path = resp.path
                  that.uploadedList.push({ 'id': file.id, 'name': file.name, 'target_name': file.target_name })
                }
                that.$toast('上传成功！')
              }
            } else {
              // 上传失败处理
              that.onError(file)
            }
          },
          Error: function(up, err) {
            var msg = err.message
            if (err.code == -600) {
              msg = '文件大小超过上传限制'
            }
            // that.$message.error(msg)
            that.$toast.fail(msg)

            // that.$message({
            //   showClose: true,
            //   message: msg,
            //   type: 'error'
            // })
            that.onError(err)
          },
          UploadComplete: function(uploader, files) {

          }
        }
      })
      that.uploader.init()
    },
    startUpload() {
      // 开始上传
      if (this.uploader.total.queued > 0) {
        this.beforeUpload()
      }
      this.uploader.start()
    },
    showImg(file, obj) {
      if (
        this.listType !== 'picture' &&
                    this.listType !== 'picture-card'
      ) {
        return
      }

      var imageType = /image.*/
      if (!imageType.test(file.type)) {
        return
      }
      // 读取File对象的数据
      try {
        // IE9 不兼容FileReader
        var reader = new FileReader()
        reader.onload = function(evt) {
          obj.url = evt.target.result
        }
        reader.readAsDataURL(file.getNative())
      } catch (error) {

      }
    },
    handlePictureCardPreview(item) {
      this.dialogImagePath = item.url
      this.dialogVisible = true
    },
    refresh() {
      // 上传按钮位置有改变时需要刷新上传组件
      this.$nextTick(() => {
        this.uploader.refresh()
      })
    },
    clickFileHandler(item) {
      if (this.fileTypeCheck(item.name)) {
        this.downloadFile(item.url, item.name)
      } else {
        this.$toast.fail('暂不支持该附件类型')
      }
    },
    downloadFile(res, fileName) {
      console.log('url---------------------------', res)
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.downloadFile(res)
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('downloadFile', String(res), (data) => {
        })
      } else {
        this.$toast('开发环境不支持')
      }
    },
    fileTypeCheck(fileName) {
      const last_len = fileName.lastIndexOf('.')
      const len = fileName.length
      const types = fileName.substring(last_len, len)
      if (types === '.pdf' || types === '.png' || types === '.jpeg' || types === '.html' || types === '.jpg') { // pdf
        return true
      } else {
        return false
      }
    }
  }
}
</script>

<style lang="css">
  .upload-plus .block-im {
    display: block !important;
  }

  .upload-plus .picture-show-contatiner {
    background-size: cover;
    background-position: center;
  }

  .upload-plus .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .upload-plus .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }

  .upload-plus .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .upload-plus .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .upload-plus .el-upload-list__item-actions .el-popover__reference {
    margin-left: 0 !important;
  }

  .upload-image-dialog {
    z-index: 100001;
  }

  .upload-image-dialog .el-dialog__header {
    padding: 0px;
  }

  .upload-image-dialog .el-dialog__body {
    padding: 5px 5px 0px 5px;
  }

</style>
